<template>
  <div class="app-comments">
    <div class="header">
      <span><i class="icon iconfont icon-fanhui"></i></span>
      <span>发表评论</span>
      <span><i></i></span>
    </div>
    <div class="cmt">
      <div class="head">
        <a href="javascript:;" class="active">登陆评论</a>
        <a href="javascript:;">匿名评论</a>
      </div>
      <div class="body">
        <div class="login active">
          <p><input type="text"></p>
          <p class="score">
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
          </p>
          <textarea name=""  cols="30" rows="8" placeholder="请输入内容"></textarea>
          <p><input type="text" placeholder="请输入验证码"></p>
          <mt-button size="large" class="submitcmt">提交评论</mt-button>
        </div>
        <div class="anonymous">
          <p><input type="text"></p>
          <p class="score">
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
            <i class="icon iconfont icon-shoucang"></i>
          </p>
          <textarea name="" id="" cols="30" rows="8" placeholder="请输入内容"></textarea>
          <p><input type="text" placeholder="请输入验证码"></p>
          <mt-button size="large" class="submitcmt">提交评论</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "submitcomment"
    }
</script>

<style scoped>
  *{
    padding: 0;
    margin: 0;
  }
  i{
    font-size:1.5rem;
  }
  .app-comments .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .cmt input{
    margin: 0;
  }
  .cmt .head{
    display:flex;
    justify-content: center;
    align-items:center;
    height:2.5rem;
    width: 100%;
    background: white;

  }
  .cmt .head a{
    display:flex;
    justify-content: center;
    align-items:center;
    width: 50%;
    height: 2.5rem;
    color: #6d6d72;
  }
  .cmt .head a.active{
    color:#019f1d;
    border-bottom: 2px solid #019f1d;
  }
  .cmt .body{
    padding:1rem;
  }
  .cmt .body>div{
    display: none;
  }
  .cmt .body>div.active{
    display: block;
  }
  .cmt .body .score{
    text-align: left;
    font-size:3rem;
    margin: 0.5rem 0;
  }
  .submitcmt{
    background: #019f1d;
    margin: 1rem 0;
    color:white
  }
</style>



