<template>
    <div class="app-memberCenter">
      <div class="header">
        <router-link to="/home">
          <span><i class="icon iconfont icon-fanhui"></i></span>
          <span>会员中心</span>
          <span></span>
        </router-link>
      </div>
      <div class="membername">
        <div><img :src="persondata.avatar" alt=""></div>
        <p>{{persondata.userName}} <span>普通会员</span></p>
      </div>
      <div class="myorder">
        <div class="head">
          <router-link to="">
            <span><i class="icon iconfont icon-single"></i>我的订单</span>
            <i class="icon iconfont icon-xiangyou"></i>
          </router-link>
        </div>
        <div class="body">
          <div>
            <router-link to="">
              <i class="icon iconfont icon-daifukuan"></i>
            <p>待付款</p>
            </router-link>
          </div>
          <div>
            <router-link to="">
              <i class="icon iconfont icon-fenqi-wo-daifahuo"></i>
            <p>待发货</p>
            </router-link>
          </div>
          <div>
            <router-link to="">
              <i class="icon iconfont icon-daishouhuo"></i>
            <p>待收货</p>
            </router-link>
          </div>
          <div>
            <router-link to="">
              <i class="icon iconfont icon-yishouhuo"></i>
            <p>已收货</p>
            </router-link>
          </div>
        </div>
      </div>
      <div class="memberinfo">
        <div class="head">
          <router-link to="">
            <span><i class="icon iconfont icon-shouhuoren"></i>收货人信息</span>
            <i class="icon iconfont icon-xiangyou"></i>
          </router-link>
        </div>
        <div class="head">
          <router-link to="">
            <span><i class="icon iconfont icon-chongzhi360"></i>我的账户</span>
            <i class="icon iconfont icon-xiangyou"></i>
          </router-link>
        </div>
        <div class="head">
          <router-link to="">
            <span><i class="icon iconfont icon-shoucang"></i>我的收藏</span>
            <i class="icon iconfont icon-xiangyou"></i>
          </router-link>
        </div>
        <div class="head">
          <router-link to="/personalInfo">
            <span><i class="icon iconfont icon-yonghu"></i>个人资料</span>
            <i class="icon iconfont icon-xiangyou"></i>
          </router-link>
        </div>
        <div class="head">
          <router-link to="/changepwd">
            <span><i class="icon iconfont icon-xiugaimima"></i>修改密码</span>
            <i class="icon iconfont icon-xiangyou"></i>
          </router-link>
        </div>
      </div>
      <div class="logout">
        <div class="head">
          <a href="javascript:;" @click="logout">
            <span><i class="icon iconfont icon-shouhuoren"></i>退出当前登录</span>
          </a>
        </div>
      </div>
    </div>
</template>

<script>
  import {Toast} from "mint-ui";
    export default {
        name: "member-center",
        data(){
          return{
            serverURL:this.server,
            persondata:{}
          }
        },
        methods:{
          getInof(){
            this.axios.get(this.serverURL+"/user/getInfo").then((result)=>{
              if(result.data.code==201){
                this.persondata=result.data.data[0];

              }else if(result.code==301){
                Toast("请先登录！")
              }
            })
          },
          logout(){
            this.axios.get(this.serverURL+"/user/logout").then((result)=>{
              if(result.data.code==501){
                Toast("退出成功！")
                this.$router.push("/home")
              }
            })
          }
        },
        created(){
          this.getInof()
        }
    }
</script>

<style scoped>
  *{margin:0px;padding:0px;}
  i{
    font-size:1.5rem;
  }
  a{
    color:#6d6d72;
  }
  .app-memberCenter .header a{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .membername{
    padding: 0.5rem 1rem;
    height:6rem;
    width: 100%;
    display:flex;
    justify-content: flex-start;
    align-items:center;
    background: white;
  }
  .membername div{
    width: 20%;
    height:80%;
    border-radius: 50%;
    background: yellow;
  }
  .membername div img{
    width: 100%;
  }
  .membername p{
    text-align: left;
    height:2rem;
    line-height: 2rem;
    font-size:1.2rem;
  }
  .membername p span{
    color:orange;
  }
  .myorder{
    margin-top:1rem;
  }
  .myorder .head a,.memberinfo .head a,.logout .head a{
    width:100%;
    height:4rem;
    padding:0 1rem;
    display:flex;
    justify-content: space-between;
    align-items: center;
    background:white;
  }
  .myorder .head{
    border-bottom: 1px dashed #aaaaaa;
  }
  .memberinfo .head+.head{
    border-top:1px dashed #aaaaaa;
  }
  .myorder .body{
    display:flex;
    justify-content: space-around;
    align-items: center;
    height: 5rem;
    background: white;
  }
  .myorder .body div{
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items:center;
  }
  .memberinfo{
    margin-top: 1rem;
  }
  .logout{
    margin-top:1rem;
  }
</style>



