<template>
    <div class="app-home">
      <myheader></myheader>
      <tabbar></tabbar>
      <loginbar></loginbar>
      <mt-swipe :auto="4000" class="carousel">
        <mt-swipe-item>
          <router-link to=""><img src="../image/carousel1.jpg" alt=""></router-link>
        </mt-swipe-item>
        <mt-swipe-item>
          <router-link to=""><img src="../image/carousel2.jpg" alt=""></router-link>
        </mt-swipe-item>
        <mt-swipe-item>
          <router-link to=""><img src="../image/carousel3.jpg" alt=""></router-link>
        </mt-swipe-item>
        <mt-swipe-item>
          <router-link to=""><img src="../image/carousel4.jpg" alt=""></router-link>
        </mt-swipe-item>
      </mt-swipe>
      <div class="plist">
        <ul>
          <li v-for="item in list">
            <router-link :to="'/pdetails?fId='+item.fId">
              <img :src="serverURL+item.spic" alt="">
              <p>{{item.title}}</p>
              <p>{{item.fdesc}}</p>
              <p>￥{{item.nUnitPrice.toFixed(2)}}/箱</p>
            </router-link>
          </li>
        </ul>
      </div>
      <backtotop></backtotop>
    </div>
</template>

<script>
    import myheader from "./subcomponents/myheader";
    import tabbar from "./subcomponents/tabbar";
    import loginbar from "./subcomponents/loginbar";
    import backtotop from "./subcomponents/backtotop"
    import {Toast} from "mint-ui";
    export default {
      name: "home",
      data() {
        return {
          list:[],
          isOnline:false,
          uname:"",
          serverURL:this.server,
        }
      },
      methods:{
        getNProducts(){
          this.axios.get(this.serverURL+"/product/normallist").then((result)=>{
            this.list=result.data
          })
        }
      },
      created(){
        this.getNProducts();
      },
      components: {
        myheader,
        tabbar,
        loginbar,
        backtotop
      }
    }
</script>

<style scoped>
  *{
    padding: 0;
    margin:0;
  }
  .carousel{
    width: 100%;
    height:10rem;
  }
  .carousel img{
    width: 100%;
  }
  .plist{
    width: 100%;
  }
  .plist ul{
    width: 100%;
    list-style:none;
    display:flex;
    justify-content: space-between;
    flex-wrap:wrap;
  }
  .plist ul li{
    margin: 1%;
    width: 48%;
    box-sizing: border-box;
    border: 1px solid #bbbbbb;
  }
  .plist ul li a{
    width: 100%;
  }
  .plist ul li a img{
    width: 100%;
  }
  .plist ul li p{
    padding: 0 0.5rem;
    text-align:left;
  }
</style>



