<template>
    <div class="backtotop">
      <a href="javascript:;" @click="backtotop"><p>top <i class="icon iconfont icon-ai-top"></i></p></a>
      <div></div>
    </div>
</template>

<script>
    export default {
        name: "backtotop",
        methods:{
          backtotop(){
            var scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
            var timer=setInterval(function(){
              if(scrollTop>=5){
                scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
                document.documentElement.scrollTop =window.pageYOffset =document.body.scrollTop=scrollTop-20;
              }else{
                clearInterval(timer);
              }
            },1)
          }
        }
    }
</script>

<style scoped>
  .backtotop{
    height:2rem;
    width: 100%;
    background: #019f1d;
   }
  .backtotop p{
    padding:0 1rem;
    text-align: right;
    color:white;
    height:2rem ;
    line-height: 2rem;
  }
  .backtotop div{
    height:2rem;
  }
</style>



