<template>
  <div class="productsMore">
    <myheader></myheader>
    <tabbar></tabbar>
    <loginbar></loginbar>
    <div class="plist2">
      <div class="sort">
        <p>
          <span data-sortby="pName" @click="sort('pName')"><span>名称</span><i class="icon iconfont" :class='[{"icon-shengxu":sortby=="pName_ASC"},{"icon-jiangxu":sortby=="pName_DESC"}]'></i></span>
          <span data-sortby="shelfTime" @click="sort('shelfTime')"><span>时间</span><i class="icon iconfont" :class='[{"icon-shengxu":sortby=="shelfTime_ASC"},{"icon-jiangxu":sortby=="shelfTime_DESC"}]'></i></span>
          <span data-sortby="nUnitPrice" @click="sort('nUnitPrice')"><span>价格</span><i class="icon iconfont" :class='[{"icon-shengxu":sortby=="nUnitPrice_ASC"},{"icon-jiangxu":sortby=="nUnitPrice_DESC"}]'></i></span>
        </p>
        <p><span>筛选<i></i></span></p>
      </div>
      <div class="list">
        <ul>
          <li v-for="item in plist">
            <router-link :to="'/pdetails?fId='+item.fId">
              <div><img :src="serverURL+item.spic" alt=""></div>
              <div>
                <p>{{item.title}}</p>
                <p>优惠价:￥{{item.nUnitPrice.toFixed(2)}}</p>
              </div>
            </router-link>
              <div><i class="icon iconfont icon-gouwuche" :data-fid="item.fId" @click="addtocart"></i></div>
          </li>
        </ul>
      </div>
    </div>
    <pagination :pageCount="pageCount" :pageNo1="pageNo1" @prev="prev" @next="next"></pagination>
    <backtotop></backtotop>
    <addconfirm :fid="fid" :isshow=isshow @hide="hide"></addconfirm>
  </div>
</template>

<script>
  import myheader from "./subcomponents/myheader";
  import tabbar from "./subcomponents/tabbar";
  import loginbar from "./subcomponents/loginbar";
  import backtotop from "./subcomponents/backtotop";
  import pagination from "./subcomponents/pagination";
  import addconfirm from "./subcomponents/addconfirm"
    export default {
        name: "products-more",
        data(){
          return{
            serverURL:this.server,
            classname:this.$route.query.classname,
            pageNo1:1,
            plist:[],
            pageCount:0,
            url:this.$route.query,
            QPP:15,
            sortby:"",
            isshow:false,
            fid:""
          }
        },
        methods:{
          backtotop(){
            var scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
            var timer=setInterval(function(){
              if(scrollTop>=5){
                scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
                document.documentElement.scrollTop =window.pageYOffset =document.body.scrollTop=scrollTop-20;
              }else{
                clearInterval(timer);
              }
            },1)
          },
          logout(){
            this.axios.get("http://127.0.0.1:3000/user/logout").then((result)=>{
              if(result.data.code==501){
                this.isOnline=false;
                this.uname="";
                Toast("退出成功！")
              }
            })
          },
          getPlist(pno){
            var classname=this.classname;
            this.pageNo1=pno;
            var pageNo=this.pageNo1;
            var sort=this.$route.query.sortby;
            this.plist=[];
            this.axios.get(this.serverURL+"/product/productlist?classname="+classname+"&pageNo="+pageNo+'&sortby='+sort+"&QPP="+this.QPP).then((result)=>{
                for(var j=0;j<result.data.length-1;j++){
                this.plist.push(result.data[j]);
                }
                var iCount=result.data[result.data.length-1].tCount;
                this.pageCount=Math.ceil(parseInt(iCount)/15);
            })
          },
          hide(){
            this.isshow=false;
          },
          addtocart(e){
            this.fid=e.target.dataset.fid;
            this.isshow=true;
            // var fid=this.$route.query.fId;
            // var uid=0;
            // var qty=this.qty;
            // var url="http://127.0.0.1:3000/shopcart/detailadd";
            // this.axios.post(url,"fid="+fid+"&uid="+uid+"&qty="+qty).then((result)=>{
            //   console.log(result)
            //   if(result.data.code==201){
            //     Toast("加入购物车成功")
            //   }
            // })
          },
          prev(){
            if(this.pageNo1>1){
              this.getPlist(this.pageNo1-1)
            }
          },
          next(){
            if(this.pageNo1<this.pageCount){
              this.getPlist(this.pageNo1+1)
            }
          },
          sort(kword){
            var pclassname = this.classname;
            if(this.$route.query.sortby==null || this.$route.query.sortby.split("_")[1]=="DESC"){
              var sort="ASC";
            }else{
              var sort="DESC";
            }
              var toUrl = `/productsMore?classname=${pclassname}&sortby=${kword}_${sort}`;
              this.$router.push(toUrl);
              this.url=this.$route.query;
          },
          tabicon(){
            var sortby=this.$route.query.sortby;
            if(sortby!=null && sortby!=undefined && sortby!="undefined") {
              this.sortby=sortby;
            }else{
              return
            }
          }
        },
        watch:{
          url(){
            this.getPlist(1);
            this.tabicon();
          }
        },
        created(){
          this.getPlist(1);
          this.tabicon()
        },
        components: {
          myheader,
          tabbar,
          loginbar,
          backtotop,
          pagination,
          addconfirm
        }
    }
</script>


<style scoped>
  *{
    padding:0;
    margin:0;
  }
  .plist2{
    background: white;
  }
  .plist2 .sort{
    padding: 0 0.5rem;
    height:3rem;
    width: 100%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    border-bottom:1px dashed #aaaaaa;
  }
  .plist2 .sort>p{
    display: flex;
    justify-content: flex-start;
  }
  .plist2 .sort>p>span{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 4rem;
    font-size:1rem;
  }
  .sort span span{
    width: 2rem;
  }
  .sort i{
    font-size:0.8rem;
  }
  .plist2 .list>ul{
    list-style:none;
  }
  .plist2 .list>ul>li{
    margin: 0.5rem 0;
    padding: 0 1rem;
    display: flex;
    justify-content:space-around;
    align-items:center;
    min-height: 8rem;
    border-bottom: 1px solid #aaaaaa;
  }
  .plist2 .list>ul>li>a{
    width: 90%;
    display: flex;
    justify-content: space-between;
  }
  .plist2 .list>ul>li>a>div:first-child{
    width:40%;
  }
  .plist2 .list>ul>li>a>div:first-child img{
    width: 100%;
  }
  .plist2 .list>ul>li>a>div:nth-child(2){
    width:60%;
    display:flex;
    flex-direction:column;
    justify-content: space-around;
  }
  .plist2 .list>ul>li>a>div:nth-child(2) p{
    text-align:left;
    margin:0.5rem;
    font-size:1rem
  }
  .plist2 .list>ul>li>a>div:nth-child(2) p:last-child{
    color:#019f1d;
  }
  .icon-gouwuche{
    color:#019f1d;
    font-size:1.5rem;
  }

</style>



