<template>
    <div class="app-register">
      <div class="header">
        <router-link to="/home"><span><i class="icon iconfont icon-fanhui"></i></span></router-link>
        <span>会员注册</span>
        <span><i ></i></span>
      </div>
      <div class="regDiv">
        <div><span>账号:</span><input type="text" placeholder="请输入账号(必填)"></div>
        <div><span>密码:</span><input type="password" placeholder="请输入密码(必填)"></div>
        <div><span>密码确认:</span><input type="password" placeholder="请输入确认密码(必填)"></div>
        <div><span>电子邮箱:</span><input type="text" placeholder="请输入邮箱(必填)"></div>
        <div><span>联系电话:</span><input type="text" placeholder="请输入联系电话(必填)"></div>
        <div><span>真实姓名:</span><input type="text" placeholder="请输入姓名(必填)"></div>
        <div class="gender">
          <span>性别:</span>
          <div>
            <label for="m" ><input type="radio" name="gender" id="m">男</label>&nbsp;
            <label for="fm" ><input type="radio" name="gender" id="fm">女</label>
          </div>
        </div>
        <div class="question">
          <span>安全问题:</span>
          <input type="text" placeholder="请输入安全问题">
        </div>
        <div><span>答案:</span><input type="text" placeholder="请输入安全问题答案"></div>
      </div>
      <div class="agree">
        <input type="checkbox">
        <span>我已经阅读并同意 <router-link to="">&lt;&lt;会员注册协议&gt;&gt;</router-link></span>
      </div>
      <mt-button size="large" class="regBtn">注册</mt-button>
    </div>
</template>

<script>
    export default {
        name: "register"
    }
</script>

<style scoped>
  *{
    padding: 0;
    margin:0px;
  }
  i{
    font-size:1.5rem;
    color:white;
  }
  .app-register .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .regDiv{
    margin: 0.5rem;
    padding:0 0.5rem;
    border:1px solid #aaaaaa;
    background: white;
  }
  .regDiv div {
    height:3rem;
    border-bottom:1px solid #aaaaaa;
    display:flex;
    align-items:center;
    justify-content: flex-start;
  }
  .regDiv div span{
    width:30%;
    text-align:left;
    height:3rem;
    line-height: 3rem;
  }
  .regDiv div input:not([type=radio]){
    width:70%;
    height: 2.9rem;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
  }
  .regDiv .gender div{
    width: 70%;
  }
  .regBtn {
    background: #019f1d;
    color:white;
    font-size:1.4rem;
    margin: 0.5rem 0;
  }
</style>



