import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import home from "../components/home"
import products from "../components/products"
import pdetails from "../components/pdetails"
import productsMore from "../components/productsMore"
import shoppingcart from "../components/shoppingcart"
import comment from "../components/comment"
import memberCenter from "../components/memberCenter"
import register from "../components/register"
import login from "../components/login"
import personalInfo from "../components/personalInfo"
import changepwd from "../components/changepwd"

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',redirect:"/home",component: HelloWorld},
    {path:'/home',component:home},
    {path:"/products",component:products},
    {path:"/pdetails",component:pdetails},
    {path:"/productsMore",component:productsMore},
    {path:"/shoppingcart",component:shoppingcart},
    {path:"/comment",component:comment},
    {path:"/memberCenter",component:memberCenter},
    {path:"/personalInfo",component:personalInfo},
    {path:"/register",component:register},
    {path:"/login",component:login},
    {path:"/changepwd",component:changepwd}
  ]
})



