<template>
  <div class="app-login">
    <div class="header">
      <router-link to="/home"><span><i class="icon iconfont icon-fanhui"></i></span></router-link>
      <span>会员登陆</span>
      <span><router-link to=""><i></i></router-link></span>
    </div>
    <div class="body">
      <div class="inputArea">
        <div><span>账号:</span><input type="text" v-model="uname" autofocus></div>
        <div><span>密码:</span><input type="password" v-model="upwd"></div>
      </div>
      <p><input type="checkbox"><span>记住我(两周内免登录)</span></p>
      <mt-button size="large" class="loginBtn" @click.prevent="login">登陆</mt-button>
      <p>
        <router-link to="">忘记密码？</router-link>
        <router-link to="/register"> 立即注册</router-link>
      </p>
    </div>
  </div>
</template>

<script>
    import {Toast} from "mint-ui";
    export default {
        name: "login",
        data(){
          return{
            serverURL:this.server,
            uname:"",
            upwd:""
          }
        },
      methods:{
        login(){
          var uname=this.uname;
          var upwd=this.upwd;
          this.axios.post(this.serverURL+"/user/login","uname="+uname+"&upwd="+upwd).then((result)=>{
            if(result.data.code==201){
                Toast("登录成功");
                this.$router.push("/home");
            }else if(result.data.code==401){
                Toast("用户名或密码不正确");
            }
          })
        }
      }
    }
</script>

<style scoped>
  .app-login .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .header a{
    color:white;
  }
  i{
    font-size:1.5rem;
  }
  .body{
    padding:0.5rem;
  }
  .body .inputArea{
    border: 1px solid #aaaaaa;
  }
  .body .inputArea div{
    height:3rem;
    display:flex;
    justify-content: flex-start;
    align-items: center;
  }
  .body .inputArea div:first-child{
    border-bottom:1px solid #aaaaaa;
  }
  .body .inputArea div span{
    width:20%;
    height:2.9rem;
    line-height: 2.9rem;
    background: white;
  }
  .body .inputArea div input{
    width: 80%;
    height:2.9rem;
    line-height: 2.9rem;
    margin: 0;
    padding: 0;
    border: none;
    outline:none;
  }
  .body p{
    margin: 0.5rem;
    text-align: left;
    font-size: 1rem;
  }
  .loginBtn{
    background: #019f1d;
    color:white;
    font-size:1.4rem;
    margin: 0.5rem 0;
  }
</style>



