<template>
    <div class="app-products">
      <myheader></myheader>
      <tabbar></tabbar>
      <loginbar></loginbar>
      <div class="searchbox">
        <input type="text" placeholder="请输入关键字">
        <div><i class="icon iconfont icon-sousuo"></i></div>
      </div>
      <header>
        <div>
          <div @click="tabshow">
            <i class="icon iconfont icon-caidan"></i>
            <p>{{currentclassname}}</p>
          </div>
          <div class="navlist" :style="{'display':tabshowdata}">
            <router-link to="/products?classname=all"><p>全部鲜果</p></router-link>
            <router-link to="/products?classname=10" ><p>进口鲜果</p></router-link>
            <router-link to="/products?classname=20" ><p>国产鲜果</p></router-link>
            <router-link to="/products?classname=30" ><p>礼盒花篮</p></router-link>
            <router-link to="/products?classname=40" ><p>宝贝水果</p></router-link>
            <router-link to="/products?classname=50" ><p>孕妇水果</p></router-link>
            <router-link to="/products?classname=60" ><p>老人水果</p></router-link>
          </div>
        </div>
        <router-link :to="'/productsMore?classname='+classname">更多</router-link></header>
      <div class="plist">
        <ul>
          <li v-for="item in plist">
            <router-link :to="'/pdetails?fId='+item.fId">
              <img :src="serverURL+item.spic" alt="">
              <p>{{item.title}}</p>
              <p>{{item.fdesc}}</p>
            </router-link>
              <p class="price"><span>￥{{item.nUnitPrice.toFixed(2)}}/箱</span><i class="icon iconfont icon-gouwuche" :data-fid="item.fId" @click="addtocart"></i></p>
          </li>
        </ul>
      </div>
      <pagination :pageCount="pageCount" :pageNo1="pageNo1" @prev="prev" @next="next"></pagination>
      <backtotop></backtotop>
      <addconfirm :fid="fid" :isshow=isshow @hide="hide" @offscroll="offscroll"></addconfirm>
    </div>
</template>

<script>
  import myheader from "./subcomponents/myheader";
  import tabbar from "./subcomponents/tabbar";
  import loginbar from "./subcomponents/loginbar";
  import backtotop from "./subcomponents/backtotop";
  import pagination from "./subcomponents/pagination";
  import addconfirm from "./subcomponents/addconfirm"
    export default {
        name: "allproducts",
        data(){
          return{
            serverURL:this.server,
            pageNo1:1,
            plist:[],
            pageCount:0,
            QPP:10,
            isshow:false,
            fid:"",
            classname:this.$route.query.classname==undefined?"all":this.$route.query.classname,
            currentclassname:"全部鲜果",
            tabshowdata:"none",
            Top:0
          }
        },
        methods:{
          backtotop(){
            var scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
            var timer=setInterval(function(){
              if(scrollTop>=5){
                scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
                document.documentElement.scrollTop =window.pageYOffset =document.body.scrollTop=scrollTop-20;
              }else{
                clearInterval(timer);
              }
            },1)
          },
          getclassname(){
            var no=this.classname;
            switch(no){
              case "all":
                this.currentclassname="全部鲜果";
                break;
              case "10":
                this.currentclassname="进口鲜果";
                break;
              case "20":
                this.currentclassname="国产鲜果"
                break;
              case "30":
                this.currentclassname="礼盒花篮"
                break;
              case "40":
                this.currentclassname="宝贝水果"
                break;
              case "50":
                this.currentclassname="孕妇水果"
                break;
              case "60":
                this.currentclassname="老人水果"
                break;
            }
          },
          getProducts(pno){
            var classname=this.classname;
            this.pageNo1=pno;
            var pageNo=this.pageNo1;
            var sort=this.$route.query.sortby;
            this.plist=[];
            this.axios.get(this.serverURL+"/product/productlist?classname="+classname+"&pageNo="+pageNo+'&sortby='+sort+"&QPP="+this.QPP).then((result)=>{
              for(var j=0;j<result.data.length-1;j++){
                this.plist.push(result.data[j]);
              }
              var iCount=result.data[result.data.length-1].tCount;
              this.pageCount=Math.ceil(parseInt(iCount)/10);
            })
          },
          prev(){
            if(this.pageNo1>1){
              this.getProducts(this.pageNo1-1)
            }
          },
          next(){
            if(this.pageNo1<this.pageCount){
              this.getProducts(this.pageNo1+1)
            }
          },
          hide(){
            this.isshow=false;
          },
          addtocart(e){
            this.fid=e.target.dataset.fid;
            this.isshow=true;
          },
          tabshow(){
            if(this.tabshowdata=="none"){
              this.tabshowdata="block";
            }else{
              this.tabshowdata="none";
            }
          },
          hideshow(){
            this.tabshowdata="none";
          },
          offscroll(scrollTop){
            console.log(222)
            this.Top = scrollTop;
          },
          setoffscroll(){
            console.log(333)
            document.body.scrollTop=this.Top;
          }
        },
        watch:{
          "$route.query"(){
            this.classname=this.$route.query.classname;
            this.getclassname();
            this.getProducts(1);
            this.hideshow();
          }
        },
        created(){
          this.getProducts(1);
          this.getclassname();
        },
        components: {
          myheader,
          tabbar,
          loginbar,
          backtotop,
          pagination,
          addconfirm
        }
    }
</script>

<style scoped>
  *{
    padding: 0;
    margin:0;
  }
  i{
    font-size:1.5rem;
  }
  a{
    color:#6d6d72;
  }

  .searchbox{
    width: 90%;
    margin: 0.5rem auto;
    position: relative;
  }
  .searchbox input{
    width: 100%;
    height: 2.4rem;
    border-radius: 1.2rem;
    padding-right:4rem;
    border:1px solid #019f1d;
  }
  .searchbox>div{
    position:absolute;
    top:0px;
    right:0px;
    height: 2.4rem;
    width:4rem ;
    border-radius:1.2rem;
    background: #019f1d;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
  .app-products header{
    position: relative;
    height:3rem;
    width: 100%;
    padding: 0 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px dashed #6d6d72;
    background: white;
  }
  .app-products header i{
    vertical-align: center;
  }
  .app-products header div>p{
    display: inline-block;
  }
  .navlist{
    position:absolute;
    top:3rem;
    background: #f2f2f2;
  }
  .navlist a{
    display: block;
    border-top:1px dashed white;
  }
  .navlist p{
    padding: 0.5rem 0.5rem 0.5rem 1.5rem;
    color:black;
    font-size:1rem;
  }
  .plist{
    width: 100%;
    background: white;
  }
  .plist ul{
    width: 100%;
    list-style:none;
    display:flex;
    justify-content: space-between;
    flex-wrap:wrap;
  }
  .plist ul li{
    margin: 1%;
    width: 48%;
    box-sizing: border-box;
    border: 1px solid #bbbbbb;
  }
  .plist ul li img{
    width: 100%;
  }
  .plist ul li p{
    margin: 0.4rem 0;
    padding: 0 0.5rem;
    text-align:left;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .plist ul li p.price{
    display: flex;
    justify-content:space-between;
    align-items: center;
    color: orange;
  }
  .plist ul li p.price i{
    color:#019f1d;
  }
</style>



