<template>
    <div class="pagination">
      <div class="prev" @click="prev">上一页</div>
      <div><span>{{pageNo1}}</span>/<span>{{pageCount}}</span></div>
      <div class="next" @click="next">下一页</div>
    </div>
</template>

<script>
    export default {
        name: "pagination",
        data(){
          return{

          }
        },
        props:["pageCount","pageNo1"],
      methods:{
          prev(){
            this.$emit("prev",1)
          },
          next(){
            this.$emit("next",1)
          }
      }
    }
</script>

<style scoped>
  .pagination{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 2rem;
    margin: 0.5rem 0;
  }
  .pagination .prev,.pagination .next{
    height: 2rem;
    width: 4rem;
    border: 1px solid #019f1d;
    border-radius: 0.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



