<template>
    <div class="app-changepwd">
      <div class="header">
        <router-link to="/memberCenter"><span><i class="icon iconfont icon-fanhui"></i></span></router-link>
        <span>修改密码</span>
        <span><i></i></span>
      </div>
      <div class="regDiv">
        <div><span>旧密码:</span><input type="password" placeholder="旧密码)" v-model="opwd"></div>
        <div><span>密码:</span><input type="password" placeholder="新密码" v-model="npwd"></div>
        <div><span>密码确认:</span><input type="password" placeholder="新密码确认" v-model="pwdconfirm"></div>

      </div>
      <mt-button size="large" class="Btn" @click="changepwd">保存</mt-button>
    </div>
</template>

<script>
  import {Toast} from "mint-ui";
    export default {
        name: "changepwd",
        data(){
          return{
            serverURL:this.server,
            opwd:"",
            npwd:"",
            pwdconfirm:""
          }
        },
        methods:{
          changepwd(){
            var opwd=this.opwd;
            var npwd=this.npwd;
            var cpwd=this.pwdconfirm;
            console.log(npwd,cpwd)
            if(opwd && npwd==cpwd){
              this.axios.post(this.serverURL+"/user/changepwd","opwd="+opwd+"&="+npwd).then((result)=>{
                console.log(result.data)
                if(result.data.code==201){
                  Toast("密码修改成功")
                }else if(result.data.code==301){
                  Toast("请先登陆")
                }
              })
            }else if(!opwd){
              Toast("旧密码不能为空")
            }else{
              Toast("两次新密码不一致")
            }
          }
        }
    }
</script>

<style scoped>
  i{
    color:white;
    font-size:1.5rem;
  }
  .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .regDiv{
    margin: 0.5rem;
    padding:0 0.5rem;
    border:1px solid #aaaaaa;
    background: white;
  }
  .regDiv div {
    height:3rem;
    border-bottom:1px solid #aaaaaa;
    display:flex;
    align-items:center;
    justify-content: flex-start;
  }
  .regDiv div span{
    width:30%;
    text-align:left;
    height:3rem;
    line-height: 3rem;
  }
  .regDiv div input:not([type=radio]){
    width:70%;
    height: 2.9rem;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
  }
  .regDiv .gender div{
    width: 70%;
  }
  .Btn {
    background: #019f1d;
    color:white;
    font-size:1.4rem;
    margin: 0.5rem 0;
  }
</style>



