<template>
  <div class="app-personalInfo">
    <div class="header">
      <router-link to="/memberCenter"><span><i class="icon iconfont icon-fanhui"></i></span></router-link>
      <span>修改资料</span>
      <span><i></i></span>
    </div>
    <div class="regDiv">
      <div><span>账号:</span><input type="text" readonly v-model="persondata.uname"></div>
      <div><span>电子邮箱:</span><input type="text" placeholder="请输入邮箱(必填)" v-model="persondata.email"></div>
      <div><span>联系电话:</span><input type="text" readonly v-model="persondata.phone"></div>
      <div><span>真实姓名:</span><input type="text" placeholder="请输入姓名(必填)" v-model="persondata.userName"></div>
      <div class="gender">
        <span>性别:</span>
        <div>
          <label for="1" ><input type="radio" name="gender" id="1"  value="1" v-model="persondata.gender">男</label>&nbsp;
          <label for="0" ><input type="radio" name="gender" id="0"  value="0" v-model="persondata.gender">女</label>
        </div>
      </div>
    </div>
    <mt-button size="large" class="Btn" @click="edit">保存</mt-button>
  </div>
</template>

<script>
  import {Toast} from "mint-ui";
    export default {
        name: "personal-info",
        data(){
          return{
            serverURL:this.server,
            persondata:{}
          }
        },
        methods:{
          getInfo1(){
            this.axios.get(this.serverURL+"/user/getInfo").then((result)=>{
              if(result.data.code==201){
                console.log(result.data.data);
                this.persondata=result.data.data[0];
              }else if(result.data.code==301){
                Toast("请先登录！")
              }
            })
          },
          edit(){
            var email=this.persondata.email;
            var userName=this.persondata.userName;
            var phone=this.persondata.phone;
            var gender=parseInt(this.persondata.gender);
            this.axios.post(this.serverURL+"/user/update","email="+email+"&userName="+userName+"&phone="+phone+"&gender="+gender).then((result)=>{
                if(result.data.code==201){
                  Toast("更新成功");
                }else if(result.code==301){
                  Toast("请新登录");
                  this.$router.push("/login");
                }
            })
          }
        },
        created(){
          this.getInfo1();
        }
    }
</script>

<style scoped>
  .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .regDiv{
    margin: 0.5rem;
    padding:0 0.5rem;
    border:1px solid #aaaaaa;
    background: white;
  }
  .regDiv div {
    height:3rem;
    border-bottom:1px solid #aaaaaa;
    display:flex;
    align-items:center;
    justify-content: flex-start;
  }
  .regDiv div span{
    width:30%;
    text-align:left;
    height:3rem;
    line-height: 3rem;
  }
  .regDiv div input:not([type=radio]){
    width:70%;
    height: 2.9rem;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
  }
  .regDiv .gender div{
    width: 70%;
  }
  .Btn {
    background: #019f1d;
    color:white;
    font-size:1.4rem;
    margin: 0.5rem 0;
  }
  i{
    color:white;
    font-size: 1.5rem;
  }
</style>



