<template>
    <div class="tabbar">
      <nav class="mui-bar mui-bar-tab" :style="position">
        <router-link to="/home"  class="mui-tab-item" :class="{active:'home'==index}">
          <span class="mui-icon mui-icon-home"></span>
          <span class="mui-tab-label">首页</span>
        </router-link>
        <router-link to="/products"  class="mui-tab-item" :class="{active:'products'==index}">
          <span class="mui-icon mui-icon-extra mui-icon-extra-holiday"></span>
          <span class="mui-tab-label">全部商品</span>
        </router-link>
        <router-link to="/products?classname=10"  class="mui-tab-item" :class="{active:'import'==index}">
          <span class="mui-icon mui-icon-extra mui-icon-extra-gift"></span>
          <span class="mui-tab-label">进口鲜果</span>
        </router-link>
        <router-link to="/products?classname=20"  class="mui-tab-item" :class="{active:'origin'==index}">
          <span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
          <span class="mui-tab-label">国产鲜果</span>
        </router-link>
        <router-link to=""  class="mui-tab-item" :class="{active:'recharge'==index}">
          <span class="mui-icon mui-icon-extra mui-icon-extra-gold"></span>
          <span class="mui-tab-label">在线充值</span>
        </router-link>
        <router-link to=""  class="mui-tab-item" :class="{active:'groupbuy'==index}">
          <span class="mui-icon mui-icon-extra mui-icon-extra-express"></span>
          <span class="mui-tab-label">团购配送</span>
        </router-link>
        <router-link to=""  class="mui-tab-item" :class="{active:'other'==index}">
          <span class="mui-icon mui-icon-gear"></span>
          <span class="mui-tab-label">其他</span>
        </router-link>
      </nav>
      <a href="javascript:;" class="button left" @click="moveright" v-show="n==-2?true:n==0?false:true"><i class="icon iconfont icon-fanhui"></i></a>
      <a href="javascript:;" class="button right" @click="moveleft" v-show="n==0?true:n==-2?false:true"><i class="icon iconfont icon-xiangyou"></i></a>
    </div>
</template>

<script>
    export default {
        name: "tabbar",
        data(){
          return{
            n:0,
            index:this.$route.path.slice(1),
          }
        },
        computed:{
          position(){
            return {left:this.n*20+"%"}
          }
        },
        methods:{
          moveleft(){
            if(this.n>-2){
              this.n--;
            }else{
              this.n=-2;
            }
          },
          moveright(){
            if(this.n<0){
              this.n++;
            }else{
              this.n=0;
            }
          }
        }
    }
</script>

<style scoped>
  .tabbar{
    position: relative;
    height: 4rem;
    overflow: hidden;
  }
  .mui-bar-tab{
    height:4rem;
    position:absolute;
    width: 140%;
    transition:all 0.3s linear;
  }
  .tabbar .button{
    position: absolute;
    display: block;
    top:0rem;
    height: 4rem;
    width:1rem;
    z-index: 100;
    font-size:2rem;
    background: rgba(150,150,150,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .tabbar .left{
    left:0px;
  }
  .tabbar .right{
    right:0rem;
  }
  i{
    font-size:1.5rem;
  }
</style>



