<template>
  <div class="app-addconfirm" >
    <div class="addconfirm" :class="{'hide':ishide}">
      <div class="detail">
        <div><img :src="serverURL+pic" alt=""></div>
        <div>
          <p>{{title}}</p>
          <p>￥{{price}}/箱</p>
        </div>
        <span><i @click="close" class="icon iconfont icon-buoumaotubiao20"></i></span>
      </div>
      <div class="QTY">
        <p>购买数量:</p>
        <div class="mui-numbox">
          <button class="mui-btn mui-btn-numbox-minus" type="button" @click="change('-')">-</button>
          <input class="mui-input-numbox" type="number" v-model="buyQTY" />
          <button class="mui-btn mui-btn-numbox-plus" type="button" @click="change('+')">+</button>
        </div>
      </div>
      <mt-button size="large" class="btn" @click="addtocart">加入购物车</mt-button>
    </div>
    <div class="blackscreen" :class="{'hide2':ishide}"></div>
  </div>
</template>

<script>
  import {Toast} from "mint-ui";
    export default {
        name: "addconfirm",
        data(){
          return {
            serverURL:this.server,
            buyQTY:1,
            ishide:true,
            title:"",
            price:0.00,
            pic:"",
            scrollTop:0
          }
        },
        props:["fid","isshow"],
        watch:{
          isshow(){
            this.ishide=!this.isshow;
            this.scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
            if(this.ishide==false){
              this.offscroll()
            }
          },
          fid(){
            this.getdetail()
          }
        },
        methods:{
          getdetail(){
            var fid=this.fid;
            this.axios.get(this.serverURL+"/product/product_detail?fId="+fid).then((result)=>{
              this.title=result.data[0].title;
              this.price=result.data[0].nUnitPrice.toFixed(2);
              this.pic=result.data[1].spic;
            })
          },
          addtocart(){
            var qty=this.buyQTY;
            var fid=this.fid;
            this.axios.post(this.serverURL+"/shopcart/detailadd","qty="+qty+"&fid="+fid).then((result)=>{
              if(result.data.code==301){
                Toast("请先登录！")
              }else if(result.data.code==201){
                Toast("添加购物车成功！")
                this.close();
              }
            })
          },
          close(){
            this.ishide=true;
            this.$emit("hide")
          },
          change(op){
            if(op=="-" && this.buyQTY>1){
              this.buyQTY--;
            }else if(op=="+"){
              this.buyQTY++;
            }
          },
          offscroll(){
            this.$emit("offscroll",this.scrollTop)
          }
        }
    }
</script>

<style scoped>
  *{
    padding: 0;
    margin:0px;
  }
  i{
    font-size:1.5rem;
  }
  .app-addconfirm{
    width: 100%;
    height:100%;
  }
  .addconfirm{
    position:fixed;
    bottom:0;
    width: 100%;
    background: white;
    transition:bottom 0.2s linear;
    z-index: 11;
  }
  div .hide{
    bottom:-45%;
  }
  .detail{
    position:relative;
    display: flex;
    justify-content: space-around;
    border-bottom:1px solid #aaaaaa;
  }
  .detail div:first-child{
    position:relative;
    width: 30%;
    top:-2rem;
    padding: 0.5rem;
    margin-left:5%;
    border:1px solid #aaaaaa;
    background: white;
  }
  .detail div:nth-child(2){
    width:55%;
  }
  .detail div:nth-child(2) p{
    height: 2rem;
    line-height: 2rem;
    text-align: left;
    font-size:1.2rem;
  }
  .detail div:nth-child(2) p:last-child{
    color:#019f1d
  }
  .detail span{
    width:10%;
  }
  .detail div img{
    width: 100%;
  }
  .addconfirm .QTY{
    width:100%;
    height:4rem;
    border-bottom: 1px solid #aaaaaa;
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding:0 0.5rem;
  }
  .addconfirm .QTY p{
    text-align: left;
    height:3rem;
    line-height: 3rem;
    font-size:1.2rem;
  }
  .btn{
    margin: 0.5rem 0;
    height:3rem;
    background: #019f1d;
    color:white;
    font-size:1.5rem;
  }
  .blackscreen{
    position:fixed;
    display: block;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background: rgba(0,0,0,0.4);
    z-index: 10;
  }
  div .hide2{
    display: none;
  }
</style>



