<template>
    <div class="loginbar">
      <router-link to="/login" v-if=!isOnline><span><i class="icon iconfont icon-yonghu"></i>登录</span></router-link>
      <router-link to="/memberCenter" v-if=isOnline><span><i class="icon iconfont icon-yonghu"></i>{{uname}}</span></router-link>
      <a href="javascript:;" @click="logout" v-if=isOnline><span><i class="icon iconfont icon-yonghu"></i>退出</span></a>
      <router-link to="/register" v-if=!isOnline><span><i class="icon iconfont icon-zhuce"></i>注册</span></router-link>
      <router-link to="/shoppingcart"><span><i class="icon iconfont icon-gouwuche"></i>购物车</span></router-link>

    </div>
</template>

<script>
  import {Toast} from "mint-ui";
    export default {
        name: "loginbar",
        data(){
          return{
            serverURL:this.server,
            isOnline:false,
            uname:""
          }
        },
        methods:{
          linktoMcenter(){
            this.axios.get(this.serverURL+"/user/linkToMCenter").then((result)=>{
              if(result.data.code==201){
                this.uname=result.data.data[0].uname;
                this.isOnline=true;
              }else if(result.code==301 || result==302){
                return
              }
            })
          },
          logout(){
            this.axios.get("http://127.0.0.1:3000/user/logout").then((result)=>{
              if(result.data.code==501){
                this.isOnline=false;
                this.uname="";
                Toast("退出成功！")
              }
            })
          }

        },
        created(){
          this.linktoMcenter()
        }
    }
</script>

<style scoped>
  .loginbar{
    height:1.5rem;
    padding: 0.2rem 0.5rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #019f1d;
  }
  .loginbar a{
    color:#019f1d;
  }
  .loginbar span{
    margin: 0 0.5rem;
  }
</style>



