<script>
  import {Toast} from "mint-ui";
  import addconfirm from "./subcomponents/addconfirm";
  import pagination from "./subcomponents/pagination";
    export default {
        name: "pdetails",
        data(){
          return{
            serverURL:this.server,
            fid:this.$route.query.fId,
            pdata:{},
            piclist:[],
            QPP:5,
            cpno:1,
            cmtlist:[],
            qty:1,
            activeda:"spec",
            isshow:false,
            pageNo1:1,
            pageCount:0,
            scorelist:[],
            cmtContent:""
          }
        },
        computed:{

        },
        methods:{
          getdetails(){
            this.axios.get(this.serverURL+"/product/product_detail?fId="+this.fid).then((result)=>{
              this.pdata=result.data[0];
              this.pdata.nUnitPrice=this.pdata.nUnitPrice.toFixed(2);
              this.pdata.oUnitPrice=this.pdata.oUnitPrice.toFixed(2);
              for(var i=1;i<result.data.length;i++){
                this.piclist.push(result.data[i])
              }
            })
          },
          getcomments(pno){
            this.pageNo1=pno;
            var url=this.serverURL+"/product/getComments?fid="+this.fid;
            url+="&QPP="+this.QPP+"&cpno="+this.pageNo1;
            this.axios.get(url).then((result)=>{
              this.cmtlist=result.data;
            })
          },
          getcmtQTY(){
            this.axios.get(this.serverURL+"/product/getCommentsQTY?fid="+this.fid).then((result)=>{
              this.pageCount=result.data[0].CommentsQTY
            })
          },
          prev(){
            if(this.pageNo1>1){
              this.getcomments(this.pageNo1-1)
            }
          },
          next(){
              if(this.pageNo1<this.pageCount) {
                this.getcomments(this.pageNo1 + 1)
              }
            },
          hide(){
            this.isshow=false;
          },
          addtocart(){
            this.isshow=true;
            // var fid=this.$route.query.fId;
            // var uid=0;
            // var qty=this.qty;
            // var url="http://127.0.0.1:3000/shopcart/detailadd";
            // this.axios.post(url,"fid="+fid+"&uid="+uid+"&qty="+qty).then((result)=>{
            //   console.log(result)
            //   if(result.data.code==201){
            //     Toast("加入购物车成功")
            //   }
            // })
          },
          active(e){
            this.activeda=e.target.dataset.name;
          },
          scorestar(e){
            this.scorelist=[];
            var n=e.target.dataset.no;
            for(var i=1;i<=n;i++){
              this.scorelist.push(i);
            }
          },
          isinscore(n){
            return this.scorelist.some(function(elem){
              return n==elem;
            })
          },
          submitcmt(){
            var cmtContent=this.cmtContent
            var score=this.scorelist.length;
            var fid=this.fid;
            if(cmtContent.length>=5){
              this.axios.post(this.serverURL+"/product/addComments","score="+score+"&fid="+fid+"&cmtContent="+cmtContent).then((result)=>{
                if(result.data.code==301){
                  Toast("请先登陆！")
                }else if(result.data.code==201){
                  Toast("评论提交成功")
                  this.getcomments(1);
                  this.getcmtQTY();
                }else if(result.data.code==101){
                  Toast("评论提交失败")
                }
              })
            }else {
              Toast("评论不能少于5个字符")
            }
          }
        },
        watch:{
          fid(){
            this.getdetails();
          }
        },
        created(){
          this.getdetails();
          this.getcomments(1);
          this.getcmtQTY()
        },
        components:{
          addconfirm,
          pagination
        }
    }
</script>

