<template>
    <div class="app-shoppingcart">
      <div class="header">
        <router-link to="/home"><span><i class="icon iconfont icon-fanhui"></i></span></router-link>
        <span>购物车</span>
        <span><i></i></span>
      </div>
      <div class="plist">
        <ul>
          <li v-for="item in list">
            <i class="icon iconfont " :class="[{'icon-xuanzhong':true==isAllChecked || isincheck(item.productId)},{'icon-weixuanzhong':true!=isAllChecked}]" :data-fid="item.productId" @click="changecheck"></i>
            <router-link :to="'/pdetails?fId='+item.fId">
              <div><img :src="serverURL+item.spic" alt=""></div>
            </router-link>
            <div>
              <p>
                <span>{{item.title}}</span>
                <span><i class="icon iconfont icon-shanchu" @click="deleteitem" :data-fid="item.productId"></i></span>
              </p>
              <p>单价:{{item.nUnitPrice.toFixed(2)}}</p>
              <div class="QTYandPrice">
                <div class="mui-numbox">
                  <button class="mui-btn mui-btn-numbox-minus" type="button" :data-fid="item.productId" data-op="-" @click="countchange">-</button>
                  <input class="mui-input-numbox" type="number" v-model="item.count"/>
                  <button class="mui-btn mui-btn-numbox-plus" type="button" :data-fid="item.productId" data-op="+" @click="countchange">+</button>
                </div>
                <span>小计:￥{{(item.count*item.nUnitPrice).toFixed(2)}}</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="bottom"></div>
      <div class="check">
        <div><i class="icon iconfont " :class="[{'icon-xuanzhong':true==isAllChecked},{'icon-weixuanzhong':true!=isAllChecked}]" @click="changeAllcheck"></i></div>
        <div>
          <p>总计:￥{{totalPrice.toFixed(2)}}</p>
          <p></p>
        </div>
        <div class="btn">结算</div>
      </div>
    </div>
</template>

<script>
  import {Toast} from "mint-ui";
    export default {
        name: "shoppingcart",
        data(){
          return{
            serverURL:this.server,
            list:[],
            isAllChecked:true,
            ischeck:[]
          }
        },
        computed:{
          totalPrice(){
            return this.list.reduce((prev,elem)=>{
              var ind=this.ischeck.indexOf(elem.productId.toString());
              if(ind>-1){
                return prev+elem.count*elem.nUnitPrice.toFixed(2);
              }else{
                return prev;
              }
            },0)
          }
        },
        methods:{
          getcartlist(){
            this.axios.post(this.serverURL+"/shopcart/init").then((result)=>{
              if(result.data.code!=301) {
                this.list=result.data;
                for(var p of this.list){
                  this.ischeck.push(p.productId.toString());
                }
              }else{
                Toast("请先登录！")
              }
            })
          },
          countchange(e){
            var fid=e.target.dataset.fid;
            for(var p of this.list){
              if(p.productId==fid){
                if(e.target.dataset.op=="-"){
                  var num=-1;
                }else if(e.target.dataset.op=="+"){
                  var num=1;
                }
                this.axios.post(this.serverURL+"/shopcart/countchange","fid="+fid+"&num="+num).then((result)=>{
                  this.list=result.data;
                  // for(var n of result.data){
                  //   console.log(111)
                  //   if(n.productId==p.productId){
                  //     console.log(222)
                  //     p.count=n.count;
                  //     console.log(this.list)
                  //   }
                  // }
                })
              }
            }
          },
          deleteitem(e){
            var fid=e.target.dataset.fid;
            this.axios.post(this.serverURL+"/shopcart/deleteItem","fid="+fid).then((result)=>{
              if(result.data.code==301){
                Toast("请先登陆！");
              }else if(result.data.code==201){
                Toast("删除成功");
                this.list.forEach(function(elem,i,arr){
                  if(arr[i].productId==fid){
                    arr.splice(i,1);
                  }
                })
              }
            })
          },
          changeAllcheck(){
            if(this.isAllChecked==true){
              this.isAllChecked=false;
              this.ischeck=[];
            }else if(this.isAllChecked==false){
              this.isAllChecked=true;
              for(var p of this.list){
                this.ischeck.push(p.productId.toString());
              }
            }
          },
          changecheck(e){
            var index=this.ischeck.indexOf(e.target.dataset.fid);
            if(index>-1){
              this.ischeck.splice(index,1)
            }else{
              this.ischeck.push(e.target.dataset.fid);
            }
            if(this.ischeck.length==this.list.length){
              this.isAllChecked=true;
            }else{
              this.isAllChecked=false;
            }
          },
          isincheck(id){
            return this.ischeck.some(function(elem){
              return elem==id;
            })
          }
        },
        created(){
          this.getcartlist()
        }
    }
</script>

<style scoped>
  *{
    padding: 0;
    margin: 0;
  }
  .bottom{
    height:4rem;
  }
  .app-shoppingcart .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height:3.5rem;
    background:#019f1d ;
    color:white;
    font-size:1.5rem;
    padding: 0 0.5rem;
  }
  .plist{
    width:100%;
    background: white;
  }
  .plist>ul{
    list-style:none;
  }
  .plist>ul>li{
    padding: 0.5rem 0.5rem;
    border-bottom:1px dashed #aaaaaa;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 8rem;
  }
  .plist>ul>li>i{
    width: 5%;
  }
  .plist>ul>li>a{
    width: 30%;
  }
  .plist>ul>li>a>div{
    width:100%
  }
  .plist>ul>li>a>div>img{
    width: 100%;
  }
  .plist>ul>li>div{
    width: 65%;
    height:7rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0.5rem 0rem;
  }
  .plist>ul>li>div>p{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size:1rem;
    margin: 0.5rem 0;
    height: 1.5rem;
    line-height: 1.5rem;
  }
  .plist>ul>li>div>div span{
    color:#019f1d;
    font-size:1rem;
  }
  .icon-xuanzhong{
    font-size:1.5rem;
    margin: 0 0.5rem;
  }
  .icon-weixuanzhong{
    font-size:1.5rem;
    margin: 0 0.5rem;
  }
  .icon-shanchu{
    font-size:1.2rem;
  }
  i{
    font-size:1.5rem;
  }
  .icon-fanhui{
    color:white;
  }
  .mui-numbox{
    height: 2rem;
    width: 6rem;
  }
  .mui-btn{
    width: 1.5rem;
    height: 2rem;
  }

  .check{
    width: 100%;
    height:4rem;
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding: 0 0.5rem;
    background: white;
    border-top:1px solid #6d6d72;
    position:fixed;
    bottom: 0px;
  }
  .check div{
    display:flex;
    justify-content:center;
    align-items:center;
  }
  .check .btn{
    width: 8rem;
    height:2.5rem;
    border-radius:0.2rem;
    background: #019f1d;
    color:white;
    font-size:1.2rem;
  }
  .check div:nth-child(2){
    flex-direction:column;
  }
  .check div:nth-child(2) p:first-child{
    color:#019f1d;
  }
  .check div:nth-child(2) p:last-child{
    color:#6d6d72;
  }
</style>



